.App {
  text-align: center;
  color: white;
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.jobayer{
  margin-top: 15vh;
  padding-top: 5vh;
  /* color: black; */
  background-color: #282c34;
  height: 500px;
}

@media (max-width:768px) {
  .jobayer{
    margin-top: 25vh;
    padding-top: 5vh;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
